@use 'styles/colors';
@import 'styles/base';

:host {
  display: block;

  .top-section {
    max-width: 440px;
  }

  .page-block {
    @extend %page-block;
  }

  .page-title {
    @extend %page-title;
    padding-bottom: 20px;
  }

  .page-description {
    @extend %page-description;
    padding-top: 0;
  }

  .page-button {
    margin-top: 10px;
  }

  .page-card {
    @extend %page-card;
    margin-top: 30px;
  }

  .card-title {
    @extend %card-title;
    padding-bottom: 0;
    white-space: nowrap;
  }

  .section-divider {
    @extend %section-divider;
  }

  .flex-row {
    display: flex;
    align-items: center;
    gap: 50px;
    justify-content: space-between;

    & > * {
      flex: 1;
    }
  }

  .payments-list {
    &__empty {
      @include proxima(16px, 400, colors.$foreground_dark)
    }

    &__item {
      display: flex;
      padding: 14px;
      background-color: #f2f2f2;
      gap: 14px;

      &-section {
        flex: 1;
        display: flex;
        align-items: center;
        justify-content: center;
        background-color: #FFFFFF;

        &:first-child {
          flex-shrink: 0;
          padding: 0 8px;
        }
      }

      &-mark {
        width: 8px;
        height: 8px;
        border-radius: 50%;
        background-color: #ee2a17;
        margin-right: 8px;
        flex-shrink: 0;
      }

      &-text {
        @include proxima(16px, 600, colors.$foreground_dark);
        text-align: center;
        padding: 10px 0;
      }

      &-button {
        flex-grow: 1;
      }

      &:not(:last-child) {
        border-bottom: 1px solid #E8E8E8;
      }
    }
  }

  @media screen and (max-width: 1080px) {
    .flex-row {
      flex-direction: column;
      gap: 20px;
      align-items: unset;
    }

    .payments-list {
      &__item {
        flex-direction: column;
        &-section {
          &:first-child {
            padding: 0 8px;
          }
        }
      }
    }
  }
}
